<template>
    <div class="about">
        <h1>{{ id ? '编辑' : '新建'}}广告位</h1>
        <el-form>
            <el-form-item label="名称">
                <el-input v-model="model.name"></el-input>
            </el-form-item>
            <el-form-item label="广告">
                <el-button size="small" @click="model.items.push({})"><i class="el-icon-plus"></i>添加广告</el-button>
                <el-row type="flex" style="flex-wrap: wrap;margin: 10px 10px" >
                    <el-col :md="24" v-for="(item, i) in model.items" :key="i" style="margin:2rem">
                        <el-form-item label="跳转链接（URL）" label-width="70">
                            <el-input v-model="item.url"></el-input>
                        </el-form-item>
                        <el-form-item label="图片" style="margin-top: 1rem">
                            <el-upload
                                    class="avatar-uploader"
                                    :action="$http.defaults.baseURL + '/upload'"
                                    :show-file-list="false"
                                    :on-success="res => $set(item, 'image', res.url) ">
                                <img v-if="item.image" :src="item.image" class="avatar">
                                <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                            </el-upload>
                        </el-form-item>
                        <el-form-item>
                            <el-button size="small" type="danger" @click="model.items.splice(i)">删除</el-button>
                        </el-form-item>
                    </el-col>
                </el-row>
            </el-form-item>
            <el-form-item>
                <el-button type="primary" @click="save">保存</el-button>
            </el-form-item>
        </el-form>
    </div>
</template>

<script>
    export default {
        props:{
            id:{}
        },
        data(){
            return {
                model:{
                    items:[]
                },
            }
        },
        methods:{
            async save(){
                if(this.id){
                    //修改
                    await this.$http.put(`rest/ads/${this.id}`, this.model)
                }else{
                    //新建
                    await this.$http.post('rest/ads', this.model)
                }
                this.$router.push('/ads/list')
                this.$message({
                    type:'success',
                    message:'保存成功'
                })
            },
            async fetch(){
                const res = await this.$http.get(`rest/ads/${this.id}`)
                this.model = Object.assign({},this.model, res.data)
            }
        },
        created() {
            this.id && this.fetch();
        }
    }
</script>

